
import Vue from 'vue'
import XHeader from 'vux/src/components/x-header/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import ToastPlugin from 'vux/src/plugins/toast/index.js'
import Scroller from 'vux/src/components/scroller/index.vue'

import { fetch } from '../../api/base'
import { checkLoginToLogin } from '@/api/login'
import PullTo from 'vue-pull-to'

Vue.use(ToastPlugin)

export default {
name: 'simple-pull-to-loadmore',
components: {
PullTo,
XHeader,
Group,
Cell,
Scroller
},
data () {
return {
dataList: [],
iconLink: '',
count: 0,
page: 1,
notloadAll: true
}
},
methods: {
loadmore (loaded) {
this.fetchData('/api/v1/agentmails?limit=2&page=1', 'Yes', loaded)
},
stateChange (state) {
if (state === 'pull' || state === 'trigger') {
this.iconLink = '#icon-arrow-bottom'
} else if (state === 'loading') {
this.iconLink = '#icon-loading'
} else if (state === 'loaded-done') {
this.iconLink = '#icon-finish'
}
},
fetchData (url, isLoad, loaded) {
let strUrl = '/api/v1/agentmails?limit=8&page=1'
url = url || strUrl
isLoad = isLoad || 'No'
fetch(url, null, response => {
let data = response.data
let msg = data.msg || data.error || '请求失败'
if (data.code !== 0) {
this.$vux.toast.text(msg, 'middle')
return false
}
this.count = data.data.count
this.dataList = this.dataList.concat(data.data.list)
if (isLoad === 'Yes') {
loaded('done')
}
if (this.dataList.length >= this.count) {
this.notloadAll = false
this.$vux.toast.text('已全部加载完成', 'middle')
}
}, error => {
console.log(error)
}, 'GET')
},
toDetail (item) {
this.$router.push('/message/detail/' + item.title + '/' + item.content + '/' + item.createdAt.substring(0, 10))
}
},
created () {
checkLoginToLogin(this)
this.fetchData()
}
}



